
.body{
    background-color: #000000;
    color: #ffffff;
    padding-bottom:80px; 
}
.body .circle_icon{
    height: 350px;
    width: 350px;
}
.body .circle_right{
    margin: 100px 0;
    text-align: left;
}
.body .circle_right .fontA{
    font-size: 28px;
    margin: 10px 0;
    color: #10a1be;
}
.body .circle_right .fontB{
    font-size: 24px;
    margin: 25px 0;
}
.body .my_input{
    width: 22vw;
    height: 36px;
    background-color: #000000;
    color: #ffffff;
    border: 1px #ffffff solid;
    text-indent:10px;
    font-size: 18px;
}
.body .my_input2{
    width: 22vw;
    height: 140px;
    background-color: #000000;
    color: #ffffff;
    border: 1px #ffffff solid;
    text-indent:10px;
    font-size: 18px;
    text-align: top;
}
.body .ddzj_icon{
    height: 50px;
    width: 200px;
}
.body .contentP{
    text-align: center;
}
.body .body_line{
    margin:0 auto;
    height: 3px;
    width: 100%;
    background: radial-gradient(#ffffff 24%, #000000 100%);
}
.body .titleInfo{
    text-align: center;
}
.body .content{
    text-align: left;
}
.body .btnReg{
    background-color: red;
    color: #ffffff;
    width: 150px;
    height: 30px;
    margin: 40px 0;
}
.body .el-input__inner{
    background-color: #000000;
    margin: 3px 0;
    color: #ffffff;
}
.body .sTitle{
    margin: 15px 0 5px 0;
}
.body .sign {
    color: red;
}
.body .ErrorInfo {
    color: red;
    font-size: 10px;
    margin: 3px 0;
}
.body .el-input {
    width: 22vw;
}
.body .el-select>.el-input {
    width: 22vw;
}

.body .el-textarea{
    background-color: #000000;
}
.body .el-textarea__inner {
    color: #ffffff;
    background-color: #000000;
    font-size: 16px;
}

.body .my_select{
    width: 22vw;
    color: #ffffff;
    background-color: #000000;
    font-size: 18px;
    text-indent:10px;
    height: 40px;
    border: 1px #ffffff solid;

}