



.header{
    text-align: left;
    height: 80px;
    line-height: 80px;
}

.header .left_logo{
    margin-top: 8px;
    margin-bottom: -8px;
}

.header .mypic{
    margin-top: 10px;
}

.header .home_icon,
.header .weixin_icon{
    height: 20px;
    line-height: 20px;
    width: 20px;
    vertical-align: middle;
    margin-top: -5px; 
}

