}
.header .title_details{
    font-size: 20px;
    height: 24px;
    line-height: 24px;
    /* vertical-align: middle; */
    /* margin-top:30px;  */
}
.header .header_title:hover{
    text-decoration: underline;
    color: red;
    cursor: hand;
}

.header .header_title2{
    font-size: 16px;
    color: #b6363a;
    line-height: 20px;
}

